/*=======================================
  MIXINS
========================================= */

@mixin font-face($family, $filename, $localfamily: null, $weight: normal, $style: normal) {
  @font-face {
    font-weight: $weight;
    font-style: $style;
    font-family: $family;
    font-display: swap;

    /* stylelint-disable */
    @if ($localfamily) {
      src:
        local($family),
        local($localfamily),
        url('../fonts/#{$filename}.woff2') format('woff2'),
        url('../fonts/#{$filename}.woff') format('woff');
    }

    @else {
      src:
        local($family),
        url('../fonts/#{$filename}.woff2') format('woff2'),
        url('../fonts/#{$filename}.woff') format('woff');
    }
  }
  /* stylelint-enable */
}


