/* Сброс стилей ul */
@mixin reset-ul() {
  padding: 0;
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
}

/**
 * Fix Internet Explorer 11 ignores list-style:none  // list-style-image: url(data:0);
 */
@mixin ie-li-nobullets() {
  list-style-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);
  list-style-position: outside;
}


@mixin clearfix() {
  &::after {
    content: '';
    display: table;
    clear: both;
  }
}


/**
 * Ретинизируем фон — переопределяем фон для ретин
 *
 * @param $img {[str]} [название изображения]
 * @param $img-w {[null|number]} [размер фона задать как у 1х]
 *
 * ПРИМЕР:
 * @include retina('../img/bg_hero--d@2x.jpg', 1200);
 *
 * ПРИМЕР ОБЫЧНОГО КОДА (без миксина)
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url('../img/bg_noise-light@2x.jpg');
    background-size: 260px auto; //размер фона задаю явно как для 1х;
  }
 */
//stylelint-disable
@mixin retina($img, $img-w: null) {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url($img);
     @if ($img-w) {
      background-size: #{$img-w}px auto;
    }
  }
}
//stylelint-enable



/**
 * АвтоПеренос внутри слова
 * Разрешить перенос по слогам
 * Требует
 *  - Установленный атрибут языка: <html lang="ru">
 *  - Эл-т с переносами д.б. display: [block||inline] (!НЕЛЬЗЯ inline-block)
 *
 * ПРИМЕР:
 * @include hyphens(); // Перенос слова по слогам — разрешить
 */
@mixin hyphens() {
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word; /* Нужно для Chrome! Non standard for webkit, overrides blink default */
  hyphens: auto;
}


/**
 * Добавляет li буллиты в виде тире «—»
 */
@mixin li-dash-bullet() {
  list-style-type: none;
  position: relative;
  margin-left: $gap-h;
  margin-top: 0.5em;

  &::before {
    content: '—\00a0';
    position: absolute;
    left: -2ch;
    top: 0;
  }
}
