.form {

  &::placeholder {
    color: #000;
    font-size: 2.4rem;
    opacity: 0.8;
  }

  &__fieldset {
    min-width: 0;
    padding: 0;
    margin: 0;
    border: none;

    &:not(:first-child) {
      margin-top: 1rem; //$line-height;
    }
  }

  &__legend {
    display: block;
    width: 100%;
    max-width: 100%; // IE-fix
    white-space: normal; // IE-fix
    // margin-top: 1rem;
    // color: currentColor;
  }
}
