.social {
  padding-top: 0;
  padding-bottom: 0;
  color: $white;
  display: flex;
  position: absolute;
  right: 40px;
  top: 35px;
  z-index: 10;

  & > *:first-child {
    margin-left: auto;
  }

  &__link {
    text-decoration: none;
    color: $white;
    width: 30px;
    height: 30px;
    padding: 2px;
    border-radius: $border-radius;
    border: 1px solid $white;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px;

    &:hover {
      transition: background-color 0.5s;
      background-color: $purple;
      border-color: $purple;
    }
  }

  /* MQ */
  @at-root {
    /* MEDIA <1550px */
    @media (max-width: 1550px) {
      // Вертикальная ориентация
      & {
        flex-direction: column;
        right: ($grid-gutter-width / 2);
        top: 40px;
      }

      &__link {
        margin: 0;

        & + & {
          margin-top: 8px;
        }
      }
    }

    /* sm- */
    @include media-breakpoint-only(sm) {
      .wtf { display: none; }
      // Горизонтальная ориентация, в рамках сетки
      & {
        right: calc(50% - #{map-get($container-max-widths, sm) / 2 - 15 }); // calc(50% + 225px);
        flex-direction: row;
      }

      &__link {
        margin: 0;

        & + & {
          margin: 0;
          margin-left: 5px;
        }
      }

      &:first-child {
        margin-left: 0;
      }
    } // End /sm

    /* xs */
    @include media-breakpoint-only(xs) {
      &__link {
        width: 40px;
        height: 40px;
      }
    } // End /xs
  } // End @at-root

  /* no-js
  ========================================= */
  @at-root {
    .no-js & {
      &__link {
        background-color: rgba($pink-dark, 0.5);
      }
    }
  } // End @no-js
}
