// Подчеркивание от центра (только для !ссылок)
@mixin hover-underline--center($h: 2px) {
  position: relative;
  text-decoration: none;

  &::after {
    content: '';
    position: absolute;
    bottom: (($h + 0.1) * -1);
    left: 0;
    width: 100%;
    height: $h;
    background-color: currentColor;
    transform: scaleX(0);
    opacity: 0;
    transition: opacity 0.2s, transform 0.2s ease-in;
  }

  &[href]:hover::after {
    transform: scaleX(1);
    opacity: 1;
  }
}


// Мигающий расходящийся контур (для кнопок, ссылок)
@mixin blinking-outline($color: $black) {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    border: 1px solid $color;
    border-radius: inherit;
    opacity: 1;
    animation: move-outline 2s infinite;
    pointer-events: none;
  }

  @keyframes move-outline {
    100% {
      transform: scale(1.15, 1.4);
      opacity: 0.1;
    }
  }
}


/**
 * If need to add animate.css>class to special @mq
 */
@mixin animate($n) {
  @if ($n == animated ) {
    animation-duration: 1s;
    animation-fill-mode: both;
  }
  // @if ($n == fadeInRight) {… animation-name: fadeInRight;}
}


/* ПРОЕКТНЫЕ
========================================= */
// Блик на кнопке
@mixin btn-flash() {
  position: relative;
  overflow: hidden;

  &::after { // & = .btn
    content: '';
    display: block;
    width: 30px;
    height: 300px;
    margin-left: 60px;
    background: rgba(#fff, 0.8);
    box-shadow: 0 0 2px #fff;
    position: absolute;
    left: -40px;
    top: -30%;
    z-index: 1;
    transform: rotate(45deg);
    transform-origin: 0 0;
    transition: all 0.3s;
    // animation: flash 3s .05s ease-in-out infinite;
    animation: flash 3s 0s linear infinite;
  }

  @keyframes flash {
    0% {
      left: -40px;
      margin-left: 0px;
    }
    30% {
      left: 110%;
      margin-left: 80px;
    }
    100% {
      left: 110%;
      margin-left: 80px;
    }
  }
}
