.field-actions {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-top: $line-height--fix;


  &__text {
    display: block;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: $font-size--small;
    color: $text-color--muted;
    line-height: $line-height;
  }

  &__btn {
    text-transform: uppercase;
    letter-spacing: 0.1em;
    margin-bottom: $gap-v;
    margin-left: auto;
    margin-right: auto;

    &:last-child {
      margin-bottom: 0;
    }

    // ЗАКРЫТЬ МОД.ОКНО
    &[data-dismiss='modal'] {
      opacity: 0.4;
      padding: calc(1.5rem - 2px) calc(3.5rem - 2px);
      transition: opacity 0.2s;

      &:hover {
        opacity: 1;
        box-shadow: none;
        background-color: $black;
        border-color: $black;
      }
    }
  }

  &__btn--on-md-down {
    @include media-breakpoint-up(lg) {
      & { display: none; }
    }
  }
}
