.hero {
  $block: &;

  padding-bottom: 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  color: $white;
  overflow: hidden;
  max-width: 90vw;
  margin-top: -175px;
  padding-top: 175px;
  z-index: 3;
  opacity: 1;


  /* Persons
  ========================================= */
  &__scene-persons {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    width: auto;
    z-index: 3;
    pointer-events: none;
    user-select: none;

    & > div {
      position: relative !important;
    }
  }

  &__scent-persons img {
    position: absolute;
    pointer-events: none;
    user-select: none;
  }

  &__left {
    margin-left: 50px;
    margin-top: 57px;
    z-index: 1;
  }

  &__pirate {
    margin-left: -92px;
    margin-top: 5px;
    z-index: 1;
  }

  &__center {
    margin-left: -160px;
    margin-top: -25px;
    z-index: 2;
  }

  &__girl-right {
    margin-left: -150px;
    z-index: 1;
  }

  &__right {
    margin-left: -158px;
    margin-top: 89px;
    z-index: 1;
  }


  /* Waves
  ========================================= */
  &__scene-waves {
    width: 100%;
    height: 227px;
    position: absolute;
    bottom: 0;
    left: 0;

    // Фоновая подложка
    &::after {
      content: '';
      width: 150vw;
      height: 100px;
      bottom: -10px;
      left: -50vw;
      background-color: $white;
      position: absolute;
    }
  }

  &__scene-waves--top {
    z-index: 4;
  }

  &__scene-waves--bottom {
    z-index: 2;
  }

  &__scene-waves > * {
    background: url('../img/bg_hero__wave@1x.svg') repeat-x 0 0;
    width: 200vw;
    height: 100%;
    position: absolute;
    margin-left: -50vw;
    margin-top: -10px;
    pointer-events: none;
  }

  &__wave-1 {
    background-position: -340px 0;
    opacity: 1;
    z-index: 3;
  }

  &__wave-2 {
    margin-top: -60px;
    background-position: -300px 0;
    opacity: 0.39;
  }

  &__wave-3 {
    margin-top: -70px;
    background-position: -110px 0;
    opacity: 0.39;
  }


  /* MQ
  ========================================= */
  @at-root {
    /* md- */
    @include media-breakpoint-down(md) {

      & {
        max-width: 100vw;
      }

      &__left { margin-left: 10px; }
    } // End /md

    /* sm- */
    @include media-breakpoint-down(sm) {
      .wtf { display: none; }

      &__scene-persons {
        width: 130vw;
      }

      &__left {
        margin-left: -200px;
      }

      &__pirate {
        margin-left: -120px;
      }

      &__right {
        margin-left: -180px;
      }

      &__scene-waves > div { background-size: 200%; }
    } // End /sm

    /* xs */
    @include media-breakpoint-only(xs) {
      &__scene-persons {
        width: 105vw;
      }

      &__left {
        margin-left: -150px;
        margin-top: 7px;
      }

      &__pirate {
        margin-left: -90px;
      }

      &__center {
        margin-left: -140px;
      }

      &__girl-right {
        margin-left: -100px;
      }

      &__right {
        margin-left: -300px;
      }
    } // End /xs
  } // End @at-root
}
