/*=========================================
  ПОДКЛЮЧЕНИЕ ШРИФТОВ
  =========================================
  AvertaCY-BlackItalic (==>AvertaCY-ExtraboldItalic+ls)(заголовки)
  AvertaCY-RegularItalic
  ? AvertaCY-Black (детские праздники)
*/

// Миксин font-face($family, $filename, $locafamily: null, $weight: normal, $style: normal)
@include font-face('AvertaCY', 'AvertaCY-RegularItalic', 'AvertaCYRegularItalic', normal, italic);
@include font-face('AvertaCY', 'AvertaCY-BlackItalic', 'AvertaCYBlackItalic', black, italic);
@include font-face('AvertaCY', 'AvertaCY-Black', 'AvertaCYBlack', bold); //black


html {
  /**
  * 1. 1rem = 10px (1rem превращается в 10пикселей). Главная фишка - возможность скейлить всю верстку под разные разрешения. Хочется сделать все на 25% больше: min-width: 1600px — font-size: 12.5px Тоже самое касается и понижения, очень удобно в адаптивной верстке. Поддержка rem 98%
  * 2. Set default font family to: sans-serif|serif|monospace.
  */
  font-size: 10px; /* 1 */  // Желательно, выключить font-size при использовании FOUT-стратегии
  font-family: $font-family--type; /* 2 */

  /*
  @include media-breakpoint-only(xs) {
    font-size: 8px;
  }
  */
}
