.slider {
  $block: &;

  $w: null;
  $h: null;
  // Arrows
  $arrow-w: 16px;
  $arrow-h: 26px;
  $arrow--prev: '../img/i_arrow-prev.svg';
  $arrow--next: '../img/i_arrow-next.svg';
  // Dots
  $place-for-dots: 70px;
  $dot-size: 19px;


  //////////////////////////
  @if ($w) { width: $w; }
  @if ($h) { height: $h; }
  // overflow: hidden;
  @if ($place-for-dots) { padding-bottom: $place-for-dots; }


  // При загрузке показываем только первый слайд (чтоб избежать скачков)
  &__slide:not(:first-child) {
    display: none;

    .slick-initialized & {
      display: block;
    }
  }

  &__slide:focus {
    outline: none;
  }

  &__arrow {
    position: absolute;
    @if ($place-for-dots) { top: calc(50% + 5px - (#{$place-for-dots} / 2)); }
    @else { top: calc(50% + 5px); }
    z-index: 1;
    width: $arrow-w;
    height: $arrow-h;
    border: none;
    opacity: 0.5;
    padding: 0;

    &:hover {
      opacity: 1;
    }

    &:focus {
      outline: none;
    }

    &--prev {
      left: 0;
      background: transparent url($arrow--prev) no-repeat 50% center;
      transform: translateY(-50%);
    }

    &--next {
      right: 0;
      background: transparent url($arrow--next) no-repeat 50% center;
      transform: translateY(-50%);
    }
  }

  &__dot {
    width: $dot-size;
    height: $dot-size;
    padding: 0;
    border: 1px solid rgba(#3d3d3e, 0.15);
    border-radius: 50%;
    background-color: $white;

    &:focus { outline: none; }

    .slick-active & {
      border-color: $black;
      background-color: $black;
    }
  }
}


.slick {
  &-dots {
    @include reset-ul();
    position: static;
    text-align: center;

    @include media-breakpoint-down(sm) {
      padding-top: ($line-height--fix / 2);
    }
  }

  &-dots li {
    display: inline-block;
    vertical-align: middle;

    &:not(:first-child) {
      margin-left: 10px;
    }
  }
}
