.modal--cto {

  .modal__intro {
    font-style: italic;
    color: $gray-light;
  }

  & .modal-body,
  .field-actions__text + .field-actions__btn,
  .field-actions__btn + .field-actions__btn {
    margin-top: $field-padding-vertical;
  }

  /* MQ */
  @at-root {
    /* sm- */
    @include media-breakpoint-down(sm) {
      text-align: center;
    } // End /sm
  } // End @at-root
}
