=========================================
  (!) CUSTOMIZATION
========================================= */
/**
 * Задача: добавить контейнеру медиа-выражения с кастомной канавкой
 *
 * АЛГОРИТМ ДЛЯ КОНЕЙНЕРА/КОЛОНКИ
 * Получить массив вида [<breakpoint>: <gutter-width>]
 * Перебрать массив:
 *  1. Соответствует <gutter-width> дефолтному?
 *  -- ДА: continue
 *  -- НЕТ: задать padding-l/r внутри mq: «desktop first» & down-наследование
 */
@mixin make-container-and-col--custom-gutter() {
  @each $breakpoint, $gutter in $grid-gutter-widths {
    @include media-breakpoint-down($breakpoint) {
      padding-right: ($gutter / 2);
      padding-left: ($gutter / 2);
    }
  }
}

@mixin make-row--custom-gutter() {
  @each $breakpoint, $gutter in $grid-gutter-widths {
    @include media-breakpoint-down($breakpoint) {
      margin-right: ($gutter / -2);
      margin-left: ($gutter / -2);
    }
  }
}

