.header {
  $block: &;

  display: flex;
  flex-direction: column;
  // min-height: 100vh;
  color: $white;
  background: $pink $gradient no-repeat 0 50%;
  background-size: 100%;
  position: relative;
  max-width: 100vw;
  overflow: hidden;

  &__container::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100vh;
    top: 0;
    left: -4px;
    background: url('../img/bg_header__circles3.svg') no-repeat 50% -100px;
    background-size: 100% auto;
    animation: linear 3s pulse infinite;
    transform-origin: 50% 145px;
    padding-top: 100%;
  }

  &__topline {
    color: $white;
    padding-top: 40px;
    opacity: 1;
    z-index: 1;
  }

  &__grid-topline {
    @include row();
    align-items: flex-start;
  }

  &__topline-left {
    @include col-ready();
    @include col(4);
  }

  &__topline-right {
    @include col-ready();
    @include col(4);
    display: flex;
    flex-direction: column;
  }

  &__logo {
    @include col-ready();
    @include col(4);
    text-align: center;
    margin-top: -12px;
    z-index: 2;
    position: relative;
  }

  &__burger {
    display: none;
    margin-top: 0;
    z-index: 5;
  }

  /* &__hero {
    flex-grow: 1;
  } */

  & a {
    // color: $white;
    text-decoration: none;
  }


  /* CIRCLES (порядок: ltr)
  ========================================= */

  &__circles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    min-height: 100vh; // путь
  }

  &__scene-circles {
    height: 100vh;

    & > div {
      position: relative !important;
    }
  }

  &__circles img {
    position: absolute;
    pointer-events: none;
    display: inline-block;
    vertical-align: top;
    user-select: none;
  }

  &__circles-big {
    & img:nth-child(1) {
      top: 225px;
      margin-left: 17.7%;
    }

    & img:nth-child(2) {
      top: 88px;
      right: -95px;
    }
  }

  &__circles-medium {
    & img:nth-child(1) {
      top: 395px;
      left: -170px;
    }

    & img:nth-child(2) {
      top: 290px;
      right: -380px;
    }
  }

  &__circles-small {
    & img:nth-child(1) {
      top: 50px;
      left: -290px;
    }

    & img:nth-child(2) {
      top: 187px;
      left: 63%;
    }

    & img:nth-child(3) {
      top: 605px;
      right: -186px;
    }
  }


  /* MQ
  ========================================= */
  @at-root {
    /* lg- */
    @include media-breakpoint-only(lg) {
      &__container::before {
        background-position: 50% -52px;
      }
    } // End /lg

    /* md */
    @include media-breakpoint-only(md) {
      &__container::before {
        background-position: 50% -34px;
      }

      &__topline-right {
        @include col(3,1);
      }
    } // End /md

    /* sm */
    @include media-breakpoint-down(sm) {
      &__container::before {
        background-position: 50% 0;
        left: -2px;
      }

      &__topline-left {
        @include col(3);
      }

      &__logo {
        @include col(6);
      }

      &__topline-right {
        @include col(3);
      }
    } // End /sm

    /* xs */
    @include media-breakpoint-only(xs) {
      &__container::before {
        background-image: url('../img/bg_header__circles.svg');
        background-position: 50% 0;
        background-size: 600px auto;
        transform-origin: 50% 50%;
      }

      &__burger {
        margin-left: ($grid-gutter-width / 2);
      }
    } // End /xs

    /* MEDIA 420<px */
    @media (max-width: 420px) {
      &__topline-left {
        @include col(2);
      }

      &__logo {
        @include col(8);
      }

      &__topline-right {
        @include col(2);
      }

      &__burger {
        margin-left: 0;
      }
    }

    /* md- */ // stylelint-disable-line
    /* @include media-breakpoint-down(md) {

      // Клон в моб.меню
      &__topline-left,
      &__topline-right {
        display: none;
      }

      &__burger {
        @include col(4);
      }

      &__header-social {
        @include col-ready();
        @include col(4);
      }
    } */
  } // End @at-root


  /* no-js */
  @at-root {
    @include media-breakpoint-down(md) {
      .no-js &__container {
          z-index: 4;
      }

      .no-js &__logo {
        order: -1;
        @include col(12);
      }

      .no-js &__topline-left,
      .no-js &__topline-right {
        @include col(12);
        margin-left: 0;
      }
    }
  } // End @no-js
}


/* Internet Explorer/Edge ≥ 11
========================================= */
//stylelint-disable
_:-ms-fullscreen,
:root {
  & .header--main {
    height: 100vh;
    overflow: hidden;
  }

  & .header__circles {
    display: none; // баг: отображает 1/4 круга
  }
}
