.burger {
  $block: &;

  display: none; // --icon-css см.отдельно
  cursor: pointer;
  padding: 0;
  border: none;
  background-color: transparent;


  &--fixed {
    z-index: ($z-index-modal - 2);

    &-js {
      opacity: 0.3;
      bottom: 27px;
      position: fixed;
      padding: 4px;
      box-sizing: content-box;
      border-radius: 3px;
      // background-color: $black;
      // color: $white;
      border: 1px solid $black;

      &:hover {
        cursor: pointer;
        opacity: 1;
      }

      /* MQ */
      @at-root {
        /* md- */
        @include media-breakpoint-down(md) {
          &,
          &:focus,
          &:active,
          &:hover {
            opacity: 0.3;
          }
        } // End /md
      } // End @at-root
    }
  }


  &--modal {
    @include media-breakpoint-up(lg) {
      display: none !important;
    }
  }
}
