.promo {
  z-index: 4;
  position: relative;
  padding-top: $gap-v--section;
  padding-bottom: $gap-v--section;

  /**
   * Разметка:
   * <parent class="@bubble-text()"><span>…</span></parent>
   * NB: Требует дочерний <span>, внутри кот. размещаем текст.
   * Может содержать несколько дочерних span'ов: <parent><span>…</span><span>…</span><parent>
   */
  $title-padding-h: 0.55em;

  @mixin bubble-text() {
    font-size: 4.457rem;
    line-height: 1.38;
    color: $white;
    transform: rotate(-3.5deg);
    display: inline-block;
    vertical-align: baseline;
    position: relative;
    font-style: italic;
    letter-spacing: 0.04em;
    text-shadow:
      0px 2px 4.85px rgba(56, 26, 59, 0.47),
      1px 1px 0 #c876c2,
      2px 2px 0 #c876c2,
      3px 3px 0 #c876c2,
      4px 4px 0 #c876c2,
      4px 5px 0 #c876c2,
      5px 6px 0 #c876c2,
      5px 6px 7.92px rgba(109, 12, 121, 0.85),
      5px 6px 15px rgba(109, 12, 121, 0.85);

    & > span {
      position: relative;
      display: inline-block;
      padding-left: $title-padding-h;
      padding-right: $title-padding-h * 1.5;
      padding-top: 0.1em;
    }

    &::after,
    & > span::after {
      display: inline-block;
      vertical-align: baseline;
      content: '';
      background: $pink $gradient no-repeat 0 0;
      position: absolute;
      z-index: -1;
      width: 100%;
      height: calc(100% + 1px);
      top: 0;
      left: 0;
      transform: skewX(-10deg);
    }

    &::after {
      left: -0.25em;
      background-image: none;
      width: 10%;
      height: 100%;
    }

    & > span:last-child {
      padding-bottom: 0.4em;
    }


    /* MQ
    ========================================= */
    @at-root {
      /* md */
      @include media-breakpoint-only(md) {
        & {
          font-size: 3.2rem;
        }
      } // End /md

      /* sm- */
      @include media-breakpoint-down(sm) {
        & {
          font-size: 3rem;
          text-align: center;
          text-transform: none;
        }

        &::after { display: none; }

        & > span {
          // padding-left: 0.35em;
          // padding-right: 0.35em;
        }
      } // End /sm
    } // End @at-root
  }

  &__grid {
    @include row();
  }

  &__column {
    @include col-ready();
    @include col(8,2);

    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      opacity: 0.17;
      display: block;
      width: 100%;
      height: 520px;
      top: -105px;
      left: 0;
      background: url('../img/bg_gear.svg') no-repeat 50% 0;
      animation: rotateCV 50s linear infinite;
    }
  }

  &__title {
    @include bubble-text();
    text-transform: uppercase;
    letter-spacing: 0.06em;
    margin-left: $grid-gutter-width;
  }

  &__title-first {
    padding-right: 1.2em;
  }

  &__title-second {
    margin-left: -0.25em;
  }

  &__tel {
    @include bubble-text();
    margin-top: 0.25em;
    margin-left: $grid-gutter-width * 0.5;
  }

  &__tel a {
    color: #fff;
    text-decoration: none;
  }

  &__tel span:last-child {
    padding-right: 3.2em;
    padding-bottom: 0.2em;
  }

  &__callback {
    margin-top: 2.8rem;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }

  &__btn {
    @include btn-flash();
  }

  /* BG
  ========================================= */
  &__bg {
    opacity: 0.17;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    max-width: 100vw;
    overflow: hidden;
    pointer-events: none;
    user-select: none;
    z-index: -1;

    & > * { position: absolute; }
  }

  &__medium {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-image: url('../img/bg_gear.svg'), url('../img/bg_gear.svg');
    background-size: 140px auto, 250px auto;
    background-position: 10% 180px, right 13% top 20px;
  }

  &__small {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: url('../img/bg_promo__circles-group.svg') no-repeat 0 0;
    background-size: 100% auto;
  }


  /* MQ
  ========================================= */
  @at-root {
    /* lg- */
    @include media-breakpoint-down(lg) {
      &__column {
        @include col(10,1);
      }

      &__medium {
        background-position: 2% 180px, right -3% top 200px;
        width: 110vw;
      }
    } // End /lg

    /* md- */
    @include media-breakpoint-down(md) {
      .promo__bg {
        top: -80px;
        height: calc(100% + 80px);
      }
    } // End /md

    /* sm- */
    @include media-breakpoint-down(sm) {
      & {
        margin-top: -100px;
        padding-top: 0;
        padding-bottom: $gap-v--section * 2;
      }

      &__column {
        @include col(12,0);
        text-align: center;

        &::before {
          animation: none;
          top: -75px;
          background-size: auto 80%;
        }
      }

      &__tel span:last-child {
        padding-left: 0.5em;
        padding-right: 0.5em;
      }

      &__tel b {
        white-space: nowrap;
      }

      &__medium {
        display: none;
      }
    } // End /sm
  } // End @at-root
}
