/* =========================================
  ТИПОГРАФИКА и стилизация по тегам
========================================= */



/* ЗНАЧЕНИЯ ПО УМОЛЧАНИЮ (кросс-проектно)
========================================= */

body {
  // ТОЛЬКО типографика!
  font-style: normal;
  font-size: $font-size;
  line-height: $line-height;
  font-family: $font-family;
  color: $text-color;
  background-color: $body-bg;
}


.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family--headings;
  font-weight: bold;
  color: currentColor;
  @include hyphens(); // Перенос слова по слогам — разрешить
}

h1,
h2,
h3 {
  margin-top: ($line-height * 1em);
  margin-bottom: ($line-height / 2 * 1em);
}

// .h4,
// .h5,
// .h6,
// h4,
// h5,
// h6 {
//   margin-top: ($line-height / 2 * 1em);
//   margin-bottom: ($line-height / 2 * 1em);
// }

.h1,
h1 {
  font-size: $font-size--h1;
  line-height: $line-height--h1;
  margin-bottom: 1.5rem;
}

.h2,
h2 {
  font-size: $font-size--h2;
  line-height: $line-height--h2;

  @if variable-exists(font-size--mobile-scale) {
    /* MOBILE  (xs & sm)
    ========================================= */
    @media (max-width: ($tablet-width - 1)) {
      font-size: ($font-size--h2 * $font-size--mobile-scale);
    }
  }
}

.h3,
h3 {
  font-size: $font-size--h3;
  line-height: $line-height--h3;
}

.h4,
h4 {
  font-size: $font-size--h4;
  line-height: $line-height--h4;
}

.h5,
h5 {
  font-size: $font-size--h5;
  line-height: $line-height--h5;
}

.h6,
h6 {
  font-size: $font-size--h6;
  line-height: $line-height--h6;
}

a {
  text-decoration: $link-decoration;
  font: inherit;
  color: $link-color;
  cursor: pointer;

  //Не реагировать на циклическую ссылку (ссылка на саму себя)
  &:not([href]) {
    cursor: default;
  }

  &:hover {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
  }
}

// .p,
p,
ul,
ol,
dl,
blockquote,
pre,
address,
figure {
  margin-top: 0.5em;
  margin-bottom: 0;
  line-height: $line-height;
}

b,
strong {
  font-weight: bold;
}

i,
em {
  font-style: italic;
}

address {
  font-style: normal;
}

ul,
ol {
  padding-left: 1.2em;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-top: 0;
  margin-bottom: 0;
}

li {
  line-height: inherit;
}

dt {
  font-weight: 700;
}

dd {
  margin-left: 0;

  @media (min-width: $body-min-width) {
    margin-left: 1.8em;
  }

  & + dt {
    margin-top: #{($line-height / 2)}em;
  }
}

hr {
  margin-top: ($line-height * 1em);
  margin-bottom: ($line-height * 1em);
  border: 0;
  border-top: 1px solid currentColor;
}

// .small,
small {
  font-size: $font-size--small;
}

.sup {
  vertical-align: super;
}

.sub {
  vertical-align: sub;
}

blockquote {
  padding: 0;
  margin-left: 0;
  margin-right: 0;
}

code,
kbd,
pre,
samp {
  font-family: $font-family--monospace;
}

code,
kbd {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: $gray-lightest;
  border-radius: $border-radius;
}

kbd kbd {
  padding: 0;
}

pre {
  display: block;
  width: 100%;
  overflow-x: auto;
  tab-size: 2;
}

table {
  vertical-align: middle;
}


mark {
  background: rgba(239, 172, 77, 0.3); //#ff0
  color: #000;
  padding: 0.1em 0.3em;
}


/*=======================================
  ПРОЕКТНЫЕ ЗНАЧЕНИЯ
=========================================
* переопределяют/дополняют значения по умолчанию */
/* stylelint-disable no-duplicate-selectors */
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  margin-top: 0;
  margin-bottom: 0;
}

/*
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {}
*/

p {
  max-width: 38em;

  & + & {
    margin-top: $line-height * 1em;
  }
}
/* stylelint-enable */

a[data-toggle='modal'] {
  text-decoration: none;
  border-bottom: 1px dashed;
}


/* MQ */
@at-root {
  /* sm- */
  @include media-breakpoint-down(sm) {
    h1,
    .h1 { font-size: ($font-size--h1 * 0.8); }

    h2,
    .h2 { font-size: ($font-size--h2 * 0.8); }
  } // End /sm
} // End @at-root
