//
// Modals
// --------------------------------------------------

// .modal-open      - body class for killing the scroll
// .modal           - container to scroll within
// .modal-dialog    - positioning shell for the actual modal
// .modal-content   - actual modal w/ bg and corners and shit


$zindex-modal: 100 !default;
$zindex-modal-background: ($zindex-modal - 1) !default;
$modal-content-color: #000 !default;
$modal-content-bg: $white !default;
$modal-content-fallback-border-color: #d3d3d3 !default; //old browsers fallback (ie8 etc)
$modal-content-border-color:  rgba(0, 0, 0, 0.2) !default;
$modal-border-radius: 6px !default;
$modal-backdrop-bg: #000 !default;
$modal-backdrop-opacity: 0.8 !default;
$modal-header-border-color: #d3d3d3 !default;
$modal-title-padding: 0 !default;
$modal-title-line-height: 1.5 !default;
$modal-inner-padding: 0 !default;
$modal-footer-border-color: #d3d3d3 !default;
$modal-width: auto !default;
$modal-close-size: 50px !default;


// Kill the scroll on the body
.modal-open {
  overflow: hidden;
}

// Container that the modal scrolls within
.modal {
  position: fixed;
  z-index: $zindex-modal;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  overflow: hidden;
  // Prevent Chrome on Windows from adding a focus outline. For details, see
  // https://github.com/twbs/bootstrap/pull/10951.
  outline: 0;
  -webkit-overflow-scrolling: touch;

  // When fading in the modal, animate it to slide down
  &.fade .modal-dialog {
    transition: transform 0.3s ease-out;
    transform: translateX(0) translateY(-25%);
  }

  &.in .modal-dialog {
    transform: translate(0, 0);
  }
}

.modal.in {
  overflow-x: hidden;
  overflow-y: auto;
}


// Shell div to position the modal with bottom padding
.modal-dialog {
  position: relative;
  margin: 10px;
  width: $modal-width;
}

// Actual modal
.modal-content {
  border: 1px solid $modal-content-fallback-border-color; //old browsers fallback (ie8 etc)
  border: 1px solid $modal-content-border-color; //stylelint-disable-line
  border-radius: $modal-border-radius;
  outline: 0; // Remove focus outline from opened modal
  color: $modal-content-color;
  background-color: $modal-content-bg;
  background-clip: padding-box;
  box-shadow: $shadow--dark;
  padding: ($modal-close-size - 10);
}

// Modal background
.modal-backdrop {
  position: fixed;
  z-index: $zindex-modal-background;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $modal-backdrop-bg;
  // Fade for backdrop
  &.fade {
    opacity: 0;
  }

  &.in {
    opacity: $modal-backdrop-opacity;
  }
}


// Modal header
// Top section of the modal w/ title and dismiss
.modal-header {
  padding: $modal-title-padding;
  border: none;
  // margin-bottom: 1.4rem;
}

// Title in header
.modal__title {
  // font-size: 2.4rem;
  // line-height: $modal-title-line-height;
  // margin-bottom: 1rem;
  margin: 0;
  display: block;
}

// Descr in header
.modal__intro {
  color: $text-color;
  position: relative;
  // text-align: center;
  // margin: 0;
  // margin-top: 1rem;
  // padding: 0;
}

// Modal body
// Where all modal content resides (sibling of .modal-header and .modal-footer)
.modal-body {
  position: relative;
  padding: $modal-inner-padding;
  // margin-top: $field-padding-vertical * 2;
}

// Footer (for actions)
.modal-footer {
  text-align: right; // right align buttons
  padding: $modal-inner-padding;
  border-top: 1px solid $modal-footer-border-color;
  //&:extend(.clearfix all); // clear it in case folks use .pull-* classes on buttons

  // Properly space out buttons
  .btn + .btn {
    margin-bottom: 0; // account for input[type="submit"] which gets the bottom margin like all other inputs
    margin-left: 5px;
  }
  // but override that for button groups
  .btn-group .btn + .btn { // stylelint-disable-line
    margin-left: -1px;
  }
  // and override it for block buttons as well
  .btn-block + .btn-block {
    margin-left: 0;
  }
}

// Measure scrollbar width for padding body during modal show/hide
.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  overflow: scroll;
  width: 50px;
  height: 50px;
}


/*
=========================================
  ВЕРТИКАЛЬНОЕ ЦЕНТРИРОВАНИЕ
========================================= */
/* stylelint-disable no-duplicate-selectors*/
.modal {
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: $grid-gutter-width / 2;
  padding-bottom: $grid-gutter-width / 2;
}

.modal-dialog {
  margin-top: auto !important; // stylelint-disable-line declaration-no-important
  margin-bottom: auto !important; // stylelint-disable-line declaration-no-important
}


/* stylelint-enable */
// End: Вертикальное центрирование


/* МЕНЬШЕ $modal-width — РЕЗИНОВАЯ ШИРИНА
========================================= */
/* stylelint-disable no-duplicate-selectors*/
@media (max-width: ($modal-width + $grid-gutter-width * 2)) {
  .modal-dialog {
    min-width: $body-min-width;
    width: calc(100% - #{$grid-gutter-width});
  }

  .modal-content {
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.5);
  }
}
/* stylelint-enable */
// End: Резиновая ширина


/*
=========================================
  NO-JS
========================================= */
// Если js не работает — оставляем блок в потоке
.no-js {
  & .modal {
    display: block;
    position: static;
    // min-height: 100vh;
    background-color: $body-bg;
  }

  & .modal-dialog {
    transform: none !important; // stylelint-disable-line
    transition: none !important; // stylelint-disable-line
    margin: 50px auto;
  }

  & .modal-content {
    box-shadow: $shadow;
  }

  & .modal .close,
  & .modal__close,
  & .modal [data-dismiss='modal'] {
    display: none;
  }
}

.modal__close {
  background-color: transparent;
  position: absolute; // stylelint-disable-line
  border: none;
  width: $modal-close-size;
  height: $modal-close-size;
  top: 0;
  right: 0;
}


/*
=========================================
АНИМАЦИИ
========================================= */
.modal { // stylelint-disable-line
  // ОТКРЫТИЕ МОД.ОКНА
  // Выезжает сверху и пружинит (применяется в JS)
  @keyframes modal-bounce {
    0% { transform: translateY(-2000px); }//stylelint-disable-line
    70% { transform: translateY(30px); }//stylelint-disable-line
    90% { transform: translateY(-10px); }//stylelint-disable-line
    100% { transform: translateY(0); }//stylelint-disable-line
  }

  // ПРИ НЕКОРРЕКТНОЙ ВАЛИДАЦИИ
  // Потрясём горизонтально форму
  @keyframes modal-shake {
    10%, 30%, 50%, 70% { transform: translateX(-10px); } // stylelint-disable-line
    20%, 40%, 60%, 80% { transform: translateX(10px); }// stylelint-disable-line
  }

  // ЗАКРЫТИЕ МОД.ОКНА
  @keyframes fadeOutRightBig { // animate.css
    from {opacity: 1;}

    to {
      opacity: 0;
      transform: translate3d(2000px, 0, 0);
    }
  }

  // ОТКРЫТИЕ MAIN-NAV
  // Выезжает слева
  @keyframes fadeInLeftBig {
    from {
      transform: translate3d(-2000px, 0, 0);
      opacity: 0;
    }

    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }

  // ЗАКРЫТИЕ MAIN-NAV
  // Заезжает влево
  @keyframes fadeOutLeftBig {
    from {
      opacity: 1;
    }

    to {
      transform: translate3d(-2000px, 0, 0);
      opacity: 0;
    }
  }


  &--bounce &__dialog {
    animation: modal-bounce 0.6s;
  }

  &--shake {
    animation: modal-shake 0.6s;
  }

  &--close &__dialog {
    animation-name: fadeOutRightBig;
    animation-duration: 1s;
    animation-fill-mode: both;
  }

  &--fadeInLeftBig &__dialog {
    animation-name: fadeInLeftBig;
    animation-duration: 0.35s;
  }

  &--fadeOutLeftBig &__dialog {
    animation-name: fadeOutLeftBig;
  }
}
