/*
=========================================
  Глобальные стили
========================================= */

// CUSTOM RESET
article,
aside,
body,
div,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  margin: 0;
  padding: 0;
}

.breadcrumbs li,
nav li {
  list-style-type: none;
}

// Ограничение размера картинок до 100% ширины родителя.
img {
  max-width: 100%;
  height: auto;
}


/* Классы-хелперы
========================================= */

.clearfix {
  @include clearfix();
}

.nooverflow {
  overflow: hidden;
}

// 2017
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.invisible {
  visibility: hidden;
}

.hyphens {
  // Перенос слова по слогам — разрешить
  @include hyphens();
}

.hidden {
  display: none;
}

.nobr {
  white-space: nowrap;
}

.nomob {
  @include media-breakpoint-down(sm) {
    & { display: none; }
  }
}


/* Кастомные ПРОЕКТНЫЕ классы
========================================= */
@keyframes pulse {
  from {
    transform: scale3d(1, 1, 1);
  }

  50% {
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}


iframe {
  max-width: 100% !important;
}


@keyframes rotateCV {
  to {
    transform: rotate(360deg);
  }
}
