/*
=========================================
  БУРГЕР ИКОНКА НА CSS
=========================================
*/
$burger-w: 30px;
$burger-h: auto;
$burger-line-w: $burger-w;
$burger-line-h: 3px;
$burger-line-gap: 5px;
$burger-color: #fff;
$burger-color--hover: null;
// ОПЦИОНАЛЬНЫЕ ЗНАЧЕНИЯ
$burger-bg: null; //[<color>|null]
$burger-bg--hover: $purple; // [<color>|null] — darken($blue-darkest, 10%)
$burger-radius: $border-radius; //[<50%>|0=none=null]
$burger-padding: 8px 5px;
$burger-border: 1px solid;

.burger {
  display: none; //flex;
  cursor: pointer;
  position: relative;
  z-index: 1;
  flex-direction: column;
  width: $burger-w;
  height: $burger-h;
  user-select: none;
  color: $burger-color; // Для наследования и простого переопределения
  @if ($burger-bg) { background-color: $burger-bg; }
  @if (burger-radius!=none and $burger-radius!=0) { border-radius: $burger-radius; }
  @if ($burger-border) { border: $burger-border; }
  @if ($burger-padding) {
    padding: $burger-padding;
    box-sizing: content-box;
  }

  /* Линии
  ========================================= */
  & > span,
  &::before,
  &::after {
    display: block;
    width: $burger-line-w;
    height: $burger-line-h;
    transition: 0.3s, background-color 0s, height 0s;
    background-color: currentColor;
  }

  &::before,
  &::after {
    content: '';
  }

  // Верхняя линия
  &::before {
    margin-bottom: $burger-line-gap; // sic!
    margin-top: auto;
  }

  // Нижняя линия
  &::after {
    margin-top: $burger-line-gap;
    margin-bottom: auto;
  }


  /* Состояния
  ========================================= */
  &:hover {
    @if ($burger-bg--hover) {
      background-color: $burger-bg--hover;
    }

    @if ($burger-color--hover) {
      color: $burger-color--hover;
    }

    @if ($burger-border and $burger-bg--hover) {
      border-color: $burger-bg--hover;
    }
  }

  &:active {
    color: transparentize($burger-color, 0.2);
  }

  &:focus {
    outline: none;
  }
}



