/*
=========================================
  Алиасы для штатных Bootstrap миксинов
========================================= */


/* CONTAINER
========================================= */
// НЕ ЗАПУСКАТЬ ВНУТРИ @mq!
@mixin container() {
  position: relative;
  @include make-container();
  @include make-container-max-widths();

  @if (type-of($grid-gutter-widths) == 'map') {
    @include make-container-and-col--custom-gutter();
  }
}


/* ROW (ФЛЕКС-КОНТЕЙНЕР)
========================================= */
// НЕ ЗАПУСКАТЬ ВНУТРИ @mq!
@mixin row() {
  @include make-row();
  // Кастомная канавка
  @if (type-of($grid-gutter-widths) == 'map') {
    @include make-row--custom-gutter();
  }
}


/* COLUMNS (ФЛЕКС-КОЛОНКА)
========================================= */
// НЕ ЗАПУСКАТЬ ВНУТРИ @mq!
@mixin col-ready() {
  @include make-col-ready();
  // Кастомная канавка
  @if (type-of($grid-gutter-widths) == 'map') {
    @include make-container-and-col--custom-gutter();
  }
}

// МОЖНО ВНУТРИ @mq
// Количество колонок по умолчанию — весь ряд
@mixin col($width: $grid-columns, $offset: null) {
  @include make-col($width);
  @if ($offset) { @include make-col-offset($offset); }
}
