$modal--main-nav__bg-color: $pink-dark;
$modal--main-nav__img: $gradient--menu;

.modal--main-nav {
  $block: '.modal';
  $close-size: 60px;

  text-align: left !important; // stylelint-disable-line declaration-no-important
  padding-top: 0;
  padding-bottom: 0;


  #{$block}__dialog {
    width: auto;
    min-width: 33.3vw;
    max-width: 100%;
    flex-basis: 100vh; //min-height: 100vh;
    height: auto;
    margin: 0;
    padding-top: $close-size;
    align-self: flex-start;
    background-color: $modal--main-nav__bg-color; //rgba(#26272d, 1);
    box-shadow: $shadow--dark;
    @if $modal--main-nav__img { background-image: $modal--main-nav__img; }
  }

  #{$block}__content {
    padding: 3rem 6rem;
    background-color: transparent;
    border: none;
    box-shadow: none;
  }

  #{$block}__close {
    width: $close-size;
    height: $close-size;
    background-color: rgba($white, 0.75);

    &:hover {
      opacity: 0.7;
      transition: 0.3s opacity;
    }

    &:active {
      outline: none;
    }
  }


  /* MQ */
  @at-root {
    /* sm- */
    @include media-breakpoint-down(sm) {
      #{$block}__dialog {
        min-width: 66.6vw;
      }
    } // End /sm
  } // End @at-root


  // NO JS
  @at-root .no-js & {
    display: none;
  }
}


/* Internet Explorer/Edge ≥ 11
========================================= */
//stylelint-disable
_:-ms-fullscreen, :root {
  & .modal--main-nav .modal__dialog {
    background-color: $modal--main-nav__bg-color; //rgba(#26272d, 1);
    box-shadow: none;
    flex-basis: auto;
  }

  & .modal--main-nav .modal__content {
    background-color: transparent;
    box-shadow: none; //$shadow--dark;
  }

  & .modal--main-nav .main-nav__item {
    @include ie-li-nobullets();
  }
}
