.btn {
  $block: &;

  $bg: #ffbd35;
  $color: $purple;
  $padding-v: $field-padding-vertical;
  $padding-h: 5rem; // $field-padding-horizontal;
  $font-size: 2.6rem;
  $text-transform: uppercase;
  $border-radius: 34.5px;

  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  margin: 0;
  padding: $padding-v $padding-h;
  font-family: inherit;
  font-size: $font-size;
  font-style: italic;
  font-weight: bold;
  line-height: 1.2;
  text-align: center;
  color: $color;
  text-transform: $text-transform;
  text-decoration: none;
  letter-spacing: 0.03em;
  white-space: normal;
  background-color: $bg;
  background-image: linear-gradient( to top, rgb(255, 198, 99) 0%, rgb(255, 189, 75) 12%, rgb(255, 180, 50) 19%, rgb(254, 209, 68) 39%, rgb(253, 237, 85) 100%);
  border: none;
  border-radius: $border-radius;
  cursor: pointer;
  user-select: none;
  box-shadow: 0 3px 0 0 rgba(207, 156, 46, 1);


  &:hover {
    text-decoration: none;
    background-color: darken($bg, 10%);
    color: $color;
    box-shadow: 0 3px 0 0 rgba(207, 156, 46, 1), $shadow;
    background-image:
      linear-gradient(rgba(#feea40, 0.5), rgba(#feea40, 0.5)),
      linear-gradient( to top, rgb(255, 198, 99) 0%, rgb(255, 189, 75) 12%, rgb(255, 180, 50) 19%, rgb(254, 209, 68) 39%, rgb(253, 237, 85) 100%);
  }

  &:active {
    // @include field-focus;
    color: rgba($color, 0.8);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.5) inset;
    outline: 0;
  }

  &:focus {
    outline: 0;
  }


  /* MQ
  ========================================= */
  @at-root {
    /* sm- */
    @include media-breakpoint-down(sm) {
      & {
        @include hyphens();
      }
    } // End /sm

    /* MEDIA <400px */
    @media (max-width: 400px) {
      & {
        font-size: 2rem;
        padding-left: 3rem;
        padding-right: 3rem;
      }
    }
  } // End @at-root


  &--disabled,
  &:disabled {
    background-color: $gray-lightest;
    border-color: $gray-light;
    cursor: not-allowed;
    opacity: $opacity-disabled;

    @at-root a.btn--disabled {
      pointer-events: none;
    }
  }


  &--w100 {
    width: 100%;
  }


  // &--ghost {
  //   color: $color;
  //   background-color: transparent;
  //   border: 1px solid currentColor;

  //   &:hover {
  //     color: darken($color, 20%);
  //     border-color: currentColor;
  //   }
  // }


  // &__icon {
  //   display: inline-block;
  //   vertical-align: middle;
  // }
}
